<template>
    <div class="header">
        <el-button @click="openMenu" v-show="isCollapse">展开</el-button> 
        <el-button @click="openMenu" v-show="!isCollapse">收缩</el-button> 
    </div>
</template>

<script>
  export default {
    data() {
      return {
        isCollapse: true
      };
    },
    methods: {
        openMenu(){
            this.isCollapse = !this.isCollapse; 
            this.$store.dispatch("setmenuFlag", this.isCollapse);
        }
    }
  }
</script>

<style scoped>
    .header {
        width: 100%;
        height: 50px;
        text-align: left;
        position: fixed;
        top: 0;
    }
    .el-menu-vertical-demo:not(.el-menu--collapse) {
        width: 200px;
        min-height: 400px;
    }
</style>


